.friendMessage {
    font-family: Roboto, sans-serif;

    .friendImageAndText {
        img {
            width: 48px;
            height: 48px;
            border-radius: 50%;
        }

        .friendText {
            position: relative;
            background: #0066CC;
            border-radius: 10px 10px 0 10px;
            padding: 10px;
            color: white;

            &:after {
                content: '';
                position: absolute;
                left: 100%;
                bottom: 0;
                width: 0;
                height: 0;
                border-bottom: 10px solid #0066CC;
                border-right: 12px solid transparent;
            }

            .friendName {}

            .friendMessageText {
                &:not(:first-child) {

                }
            }
        }
    }
    .friendTime {}
}




